import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AscRatesComponent } from './asc-rates.component';

const routes: Routes = [
  {
    path: '',
    component: AscRatesComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AscRatesRoutingModule { }
